import Vue from 'vue';
import Router from 'vue-router';
import Hello from 'components/Hello.vue';
import World from 'components/world/world.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/world',
      name: 'World',
      component: World
    }
  ]
});
